li {

  &.book__card {
    padding: 0.5rem;
    background-color: rgba(211, 206, 206, 0.9);
    width: 350px;
    height: 300px;
    margin: 1rem 1rem 1rem 1rem;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.3), 0 6px 20px 0 rgba(0, 0, 0, 0.3);

    div.images {
      display: flex;
      justify-content: space-between;
      margin-bottom: 1rem;
    }
    h2 {
      text-align: left;
    }
    img {
      height: 190px;
      width: 150px;
      margin-left: 0.5rem;
    }
  }
}