div.likeBtn {
  height: 40px;
  width: 40px;
  background-size: cover;
  mask-image: url('./like.svg'); 
  mask-size: contain;
  mask-position: center;
  mask-repeat: no-repeat;
  background-color: black;

  &.red {
    background-size: cover;
    mask-image: url('./like.svg'); 
    mask-size: contain;
    mask-position: center;
    mask-repeat: no-repeat;
    background-color: red;
  }
}
